import React from 'react';
import {
  BrowserRouter as Router, 
  Redirect, 
  Route, 
  Switch
} from "react-router-dom";

import SignUp from './Components/signup';
import Login from './Components/login';

class App extends React.Component {
  render() {
    return (
      <Router>
        <div>
          <Switch>
          <Route path={"/"} exact>
            <h1>This is Homepage</h1>
          </Route>
          <Route path={"/login"}>
            <Login/>
          </Route>
          <Route path={"/signup"}>
            <SignUp/>
          </Route>
          <Route path={"/about-us"}>
            This is About us
          </Route>
          <Route path={"/contact-us"}>
            This is contact us
          </Route>
          <Route path={"/404"}>
            <h1>Page not found</h1>
          </Route>
          <Route path={"/**"}>
            <Redirect to={"/404"}/>
          </Route>
          </Switch>
        </div>
      </Router>
    );
  }
}

export default App;